import React, { useEffect, useRef } from "react";
import { useState } from "react";
import {
  Button,
  Collapse,
  Container,
  Form,
  FormControl,
  Modal,
  Nav,
  Navbar,
  NavDropdown,
} from "react-bootstrap";

import API from "../../Content/API/API.json";
import {
  loginPost,
  registerPost,
} from "../../Services/Axios/axiosConnectService";
import { User } from "../../Models/User";
import { JsonService } from "../../Services/JSON/JsonService";
import { UserRegisterCredentials } from "../../Models/User/UserRegisterCredentials";
import { LocalStorageService } from "../../Services/Storage/LocalStorageService";
import { Link } from "react-router-dom";

import TopSoftIconLight from "../../Content/assets/png/Logo White.png";
import TopSoftIconDark from "../../Content/assets/png/Logo Black.png";


type HeaderBarProps = {
  language: String;
  setLanguage?: Function;
  user?: User;
  setUser: React.Dispatch<React.SetStateAction<User | undefined>>;
};

export const HeaderBar = (props: HeaderBarProps) => {
  const jsonService = JsonService.getInstance();
  const localStorageService = LocalStorageService.getInstance();

  const wrapper = useRef("idk");

  // const [openRegister, setOpenRegister] = useState(false);
  const [openLogin, setOpenLogin] = useState<boolean>(false);
  const [openRegister, setOpenRegister] = useState<boolean>(false);



  const [loginUsername, setLoginUsername] = useState<string>("");
  const [loginPassword, setLoginPassword] = useState<string>("");
  const [loginRememberMe, setLoginRememberMe] = useState<boolean>(false);

  const [registerUsername, setRegisterUsername] = useState<string>("");
  const [registerPassword, setRegisterPassword] = useState<string>("");
  const [registerConfirmPassword, setRegisterConfirmPassword] = useState<string>("");
  const [registerEmail, setRegisterEmail] = useState<string>("");

  localStorageService.setCurrentLanguage("ro");

  const handleClose = () => {
    setOpenLogin(false);
    setOpenRegister(false);
  };
  const handleLoginSubmit = () => {
    var encodedString = Buffer.from(
      `${loginUsername}:${loginPassword}`
    ).toString("base64");
    var headers = {
      Authorization: "Basic " + encodedString,
      RememberMe: loginRememberMe,
    };

    loginPost(headers).then((response: any) => {
      if (response && response.status == 200) {
        var client = response.data;
        props.setUser(client as User);
        localStorageService.logInUser(client.token);
        handleClose();
        return;
      }
      //TODO: Error message for error code 400 and 500
    });
  };

  const handleLoginRegister = () => {
    var user: UserRegisterCredentials = {
      username: registerUsername,
      password: registerPassword,
      confirmPassword: registerConfirmPassword,
      emailAddress: registerEmail,
    };
    debugger;
    registerPost(user).then((response: any) => {
      if (response && response.status < 300) {
        props.setUser(response.data);
        localStorageService.logInUser(response.data.token);
        handleClose();
        return;
      }
    });
  };

  return (
    <React.Fragment>
      <Navbar className="blackBG" expand="lg" sticky="top">
        <Navbar.Brand href="#">
            
                <img src={ (window.matchMedia('(prefers-color-scheme: dark)').matches)? TopSoftIconLight : TopSoftIconDark} height="70rem" alt="NavIcon"/>    
         
        </Navbar.Brand>
        
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">

        <Nav className="ml-auto my-2 my-lg-0" >
            <Nav.Link href="#action1"><span className="menuOption">Despre Noi</span></Nav.Link>
            <Nav.Link href="#action2"><span className="menuOption">Servicii</span></Nav.Link>
            <Nav.Link href="#action2"><span className="menuOption">Contact</span></Nav.Link>
        </Nav>

        <Nav className="ml-auto my-2 my-lg-0" >
            <Nav.Link href="#"><span className="menuOption" onClick={()=>{setOpenLogin(true)}}>Login</span></Nav.Link>
            <Nav.Link href="#"><span className="menuOption" onClick={()=>{setOpenRegister(true)}}>Conectare</span></Nav.Link>
            
            <Nav.Link href="#"><span className="menuOption">Tara</span></Nav.Link>
        </Nav>
          
        </Navbar.Collapse>
      </Navbar>

     
      <Modal show={openLogin} onHide={handleClose} ref={wrapper}>
        <Modal.Header closeButton>
          <Modal.Title> {jsonService.getTextInLang("Connect")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form
            method="post" id="loginForm"action={API.host + "Account/Login"}>
            <Form.Group controlId="loginUsername">
              <Form.Label>{jsonService.getTextInLang("Username")}</Form.Label>
              <Form.Control
                type="text"
                onChange={(e) => setLoginUsername(e.target.value)}
                placeholder="Username..."
              />
            </Form.Group>
            <Form.Group controlId="loginPassword">
              <Form.Label>{jsonService.getTextInLang("Password")}</Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setLoginPassword(e.target.value)}
                placeholder="Password..."
              />
            </Form.Group>
            <Form.Check
              type="checkbox"
              id={`rememberMe`}
              onChange={(e) => setLoginRememberMe(e.target.checked)}
              label={jsonService.getTextInLang("KeepMeLogged")}
            />
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            {jsonService.getTextInLang("Close")}
          </Button>
          <Button variant="primary" onClick={() => handleLoginSubmit()}>
            {jsonService.getTextInLang("Login")}
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={openRegister} onHide={handleClose} ref={wrapper}>
        <Modal.Header closeButton>
          <Modal.Title> {jsonService.getTextInLang("Register")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form
            method="post"
            id="loginForm"
            action={API.host + "Account/Login"}
          >
            <Form.Group controlId="registerUsername">
              <Form.Label>{jsonService.getTextInLang("Username")}</Form.Label>
              <Form.Control
                type="text"
                onChange={(e) => setRegisterUsername(e.target.value)}
                placeholder="Username..."
              />
            </Form.Group>
            <Form.Group controlId="registerPassword">
              <Form.Label>{jsonService.getTextInLang("Password")}</Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setRegisterPassword(e.target.value)}
                placeholder="Password..."
              />
            </Form.Group>
            <Form.Group controlId="registerConfirmPassword">
              <Form.Label>
                {jsonService.getTextInLang("ConfirmPassword")}
              </Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setRegisterConfirmPassword(e.target.value)}
                placeholder="Password..."
              />
            </Form.Group>
            <Form.Group controlId="registerEmailAddress">
              <Form.Label>{jsonService.getTextInLang("Email")}</Form.Label>
              <Form.Control
                type="text"
                onChange={(e) => setRegisterEmail(e.target.value)}
                placeholder="Email@test.com"
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            {jsonService.getTextInLang("Close")}
          </Button>
          <Button variant="primary" onClick={() => handleLoginRegister()}>
            {jsonService.getTextInLang("Register")}
          </Button>
        </Modal.Footer>
      </Modal>
    </React.Fragment>
  );
};
