import {useEffect, useState} from "react";
import {User} from "../Models/User";
import {HeaderBar} from "./Header/HeaderBar"
import {verifyToken} from '../Services/Axios/axiosConnectService';
import {BrowserRouter, Route, Switch} from "react-router-dom";
import { AccountView } from "./Pages/AccountView";
import { IndexView } from "./Pages/IndexView";

export const Topsoft = () => {
    const [user,setUser] = useState < User > ();
    const [fetch,setFetch] = useState(true);

    useEffect(() => {
        if (localStorage.getItem("token") !== null && fetch) {
            setFetch(false);
            verifyToken({
                Authorization: "Bearer " + localStorage.getItem("topsoft_token")
            }).then((response : any) => {
                if(response){
                    if (response.status < 300) {
                        setUser(response.data as User);
                        return;
                    }
                }

                setUser(undefined);
    
            });
        }
        

    }, [fetch]);

    var isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)


    return <div>
        <BrowserRouter>
            <HeaderBar language="ro" user={user} setUser={setUser}/>
        <Switch>
            
            <Route path="/Account" component={AccountView}/>
            <Route exact path="/" component={IndexView} />
            <Route />
        </Switch>
        </BrowserRouter>
  
        
    </div>
}